*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

.page {
  height: 100%;
}

.page__body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: stretch;
  min-width: 200px;
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #000000;
  background-color: #ffffff;
  background-image: linear-gradient(to top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url("../images/background.jpg");
  background-repeat: repeat;
  background-size: 100%;
  background-position: center;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
    max-width: 1220px;
  }
}

.header {
  color: #000000;
  border-bottom: 1px solid #2d2d44;
}

.header__title {
  font-size: 32px;
  text-align: center;
}

.main {
  flex: 1 0 auto;
  background: linear-gradient(180deg, rgba(222, 237, 255, 0.6) 0%, rgba(166, 204, 250, 0.5) 100%);
}

.intro__content {
  padding: 30px;
  background-color: #ffffff;
}

.intro__title {
  margin: 0 0 20px;
}

.types-solids__list {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-items: stretch;
  gap: 40px 60px;
  padding: 0;
  list-style: none;
}
@media (min-width: 1440px) {
  .types-solids__list {
    grid-template-columns: repeat(2, 1fr);
  }
}

.solids {
  display: flex;
  justify-content: center;
  align-items: center;
}

.solids__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .solids__list {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media (min-width: 1440px) {
  .solids__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.solids__item {
  padding: 20px;
  text-align: center;
  background: #eeeeee;
}

.solid {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-items: start;
  padding: 30px;
  background: #eeeeee;
}
@media (min-width: 768px) {
  .solid {
    grid-template-columns: 1fr auto;
  }
}
@media (min-width: 1440px) {
  .solid {
    grid-template-columns: auto 1fr;
  }
}

.solid__title {
  margin: 0 0 10px;
}

@media (min-width: 768px) {
  .solid__image {
    grid-column: 2;
    grid-row: span 3;
  }
}
@media (min-width: 1440px) {
  .solid__image {
    grid-column: 1;
    grid-row: 1;
  }
}

@media (min-width: 1440px) {
  .solid__text {
    grid-column: span 2;
  }
}

canvas:hover {
  cursor: pointer;
}

canvas:active {
  cursor: move;
}

.footer {
  background: #2d2d44;
  color: #ffffff;
}

.footer__text {
  text-align: center;
}